<template>
    <div class="modal">
        <div class="modal-main" :class="{'modalShow':modalShow}">
            <div class="modal-inner">
                <div class="modal-top">
                    <button v-if="closeable==undefined || closeable==true"  @click="closeModal" class="close-btn"></button>
                </div>
                <div class="modal-content">
                    <div class="confirm-tips">
                        <slot name="message"></slot>
                    </div>
                    <div class="btn-group">
                        <slot name="btnGroup"></slot>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal-overlay" v-if="modalShow" @click="closeModal"></div>
    </div>
</template>

<script>
    export default {
        props: ['modalShow','closeable'],
        name: 'Modal',
        watch:{
            modalShow: function (newVal) {
                if (newVal){
                        document.body.style.overflow='hidden'
                }else {
                    setTimeout(()=>{
                        document.body.style.overflow='auto'
                    },300)
                }
            },
        },
        mounted(){
            if (this.closeable==true || this.closeable==undefined) {
                document.onkeyup = (e) => {
                    if (e.keyCode===27){
                        this.closeModal()
                    }
                }
            }
        },
        methods: {
            closeModal() {
                if (this.maskCloseable==undefined || this.maskCloseable==true ) {
                    this.$emit('close')
                }
            },
        }
    }
</script>

<style lang="scss" scoped>
    .modal {
        .modal-main {
            transform: translate(-50%,-50%);
            width: 512px;
            /*height: 260px;*/
            position: fixed;
            left: 50%;
            top: 50%;
            z-index: 3000;
            visibility: hidden;
            .modal-inner {
                width: inherit;
                height: inherit;
                background-color: #ffffff;
                /*border: 1px solid #e9e9e9;*/
                transition: all .2s ease-out;
                transform: translateY(30%);
                opacity: 0;
                box-shadow: 0 0 10px #999;
                position: relative;
                padding: 60px 50px;
                .modal-top {
                    .close-btn {
                        position: absolute;
                        right: 0;
                        top: 0;
                        width: 34px;
                        height: 34px;
                        -webkit-transform: scale(.5);
                        -moz-transform: scale(.5);
                        -ms-transform: scale(.5);
                        -o-transform: scale(.5);
                        transform: scale(.5);
                        &::before{
                            -webkit-transform: rotate(-45deg);
                            -moz-transform: rotate(-45deg);
                            -ms-transform: rotate(-45deg);
                            -o-transform: rotate(-45deg);
                            transform: rotate(-45deg);
                        }
                        &::after {
                            -webkit-transform: rotate(45deg);
                            -moz-transform: rotate(45deg);
                            -ms-transform: rotate(45deg);
                            -o-transform: rotate(45deg);
                            transform: rotate(45deg);
                        }
                        &::before,&::after {
                            position: absolute;
                            top: 16px;
                            left: -4px;
                            content: '';
                            width: 44px;
                            height: 3px;
                            background-color: #d7d8dd;
                            transition: transform .5s ease-out,-webkit-transform .5s ease-out;
                        }
                        &:hover::after{
                            -webkit-transform: rotate(-45deg);
                            -moz-transform: rotate(-45deg);
                            -ms-transform: rotate(-45deg);
                            -o-transform: rotate(-45deg);
                            transform: rotate(-45deg);
                        }
                        &:hover::before {
                            -webkit-transform: rotate(-135deg);
                            -moz-transform: rotate(-135deg);
                            -ms-transform: rotate(-135deg);
                            -o-transform: rotate(-135deg);
                            transform: rotate(-135deg);
                        }
                        &:hover::before,&:hover::after {
                            background-color: #666;
                            transition: all .5s ease-out;
                        }
                    }
                    button {
                        background: none;
                        border: 0;
                        -webkit-box-sizing: border-box;
                        -moz-box-sizing: border-box;
                        box-sizing: border-box;
                        color: inherit;
                        cursor: pointer;
                        font: inherit;
                        line-height: inherit;
                        overflow: visible;
                        vertical-align: inherit;
                        outline: none;
                    }
                }
                .modal-content {
                    text-align: center;
                    .confirm-tips {
                        font-size: 18px;
                        font-weight: 200;
                        color: #666666;
                        min-height: 5em;
                    }
                    .btn-group {
                        div {
                            display: flex;
                            justify-content: center;
                            span {
                                width: 45%;
                                margin: 0 2.5%;
                                font-size: 14px;
                            }
                        }
                    }
                }
            }
        }
        .modal-overlay {
            position: fixed;
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            background-color: rgba(0,0,0,.5);
            z-index: 2999;
            transition: background-color .3s ease-out;

        }
        .modalShow {
            visibility: visible;
            .modal-inner {
                transform: translateY(0);
                opacity: 1;
                transition: all .5s ease-out;
            }
        }
    }
</style>
